import React, { Component } from 'react'
import axios from 'axios';
import './CreateMat.css';

const host = '192.168.1.73';

const apiGetMats = 'http://' + host + ':4000/api/mats';
const apiPostMats = 'http://' + host + ':4000/api/mats';
const apiDeleteMats = 'http://' + host + ':4000/api/mats/';



export default class CreateMat extends Component {

    state = {
        mats: [],
        matname: ''
    }

    async componentDidMount() {
        this.getMats();
    }

    getMats = async () => {
        const res = await axios.get(apiGetMats);
        this.setState({mats: res.data});
    }

    onChangeMatname = (e) => {
        this.setState({
            matname: e.target.value
        })
    }

    onSubmit = async e => {
        e.preventDefault();
        await axios.post(apiPostMats, {
            matname: this.state.matname
        })
        this.setState({matname: ''});
        this.getMats();
    }

    deleteMat = async (id) => {
        await axios.delete(apiDeleteMats + id);
        this.getMats();
    }

    render() {
        return (
            <div className="main">
                <form onSubmit={this.onSubmit} className="form"> 
                    <h3>Crea nueva Materia</h3>
                    <input type="text" className="text" onChange={this.onChangeMatname} placeholder="Materia:"/>
                    <button type="submit" className="button">Guardar</button>
                </form>
                <div className="second">
                    <h3>Materias definidas</h3>
                    <ul>
                        {
                        this.state.mats.map(mat => <li className="" key={mat._id} onDoubleClick={() => this.deleteMat(mat._id)}
                        >
                            {mat.matname}
                        </li>)
                        }
                    </ul>
                </div>
            </div>
        )
    }
}
