import React, { Component } from 'react'
import axios from 'axios'
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'

import './CreateNote.css';

const host = '192.168.1.73';

const apiMats = 'http://' + host + ':4000/api/mats';
const apiCreateNote = 'http://' + host + ':4000/api/notes';
const apiPutNotes = 'http://' + host + ':4000/api/notes/';

export default class CreateNote extends Component {

    state= {
        mats: [],
        matSelected: '',
        title: '',
        content: '',
        date: new Date(),
        editing: false,
        _id: ''
    }

    async componentDidMount() {
        const res = await axios.get(apiMats);
        this.setState({
            mats: res.data.map(mat => mat.matname),
            matSelected: res.data[0].matname
        })
        if (this.props.match.params) {
            const res = await axios.get(apiPutNotes + this.props.match.params.id);
            this.setState({
                title: res.data.title,
                content: res.data.content,
                date: new Date(res.data.date),
                matSelected: res.data.mat,
                editing: true,
                _id: this.props.match.params.id
            })
        }
    }

    onSubmit = async (e) => {
        e.preventDefault();
        const newNote = {
            title: this.state.title,
            content: this.state.content,
            date: this.state.date,
            mat: this.state.matSelected
        };
        if (this.state.editing) {
            await axios.put(apiPutNotes + this.state._id, newNote);
        } else {
            await axios.post(apiCreateNote, newNote);
        }
        window.location.href = '/';
       }

    onInputChange = e => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    onChangeDate = date => {
        this.setState({date});
    }

    render() {
        return (
            <form onSubmit={this.onSubmit} className="formCreateNote">
                <h2 className="black">Crea una nota</h2>
                {/* SELECT USER */}
                <select className="select" name="matSelected" onChange={this.onInputChange} value={this.state.matSelected}>
                    {
                        this.state.mats.map(mat =>
                            <option key={mat} value={mat}>
                                {mat}
                            </option>)
                    }
                </select>
                <input type="text" className="text" placeholder="Título" name="title" required onChange={this.onInputChange} value={this.state.title}/>
                <textarea name="content" className="text" placeholder="Añade una descripción" required onChange={this.onInputChange} value={this.state.content}></textarea>
                <DatePicker className="text datepicker" selected={this.state.date} onChange={this.onChangeDate}/>
                <button type="submit" className="button buttoncreate">
                    Guardar Nota
                </button>
            </form>
        )
    }
}
